.cart {
  width: 25%;
  min-width: 280px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cart__total {
  background-color: #212120;
  height: 10%;
  width: 100%;
}
.cart__total h2 {
  font-size: 30px;
  color: #F9800D;
  text-align: center;
}
.cart__cards {
  height: 80%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  background-color: rgb(240, 240, 240);
}
.cart__cards::-webkit-scrollbar {
  display: none;
}
.cart__cards__card {
  position: relative;
  width: 90%;
  min-height: 80px;
  border-radius: 5px;
  margin-top: 20px;
  background-color: white;
  box-shadow: 0px 5px 5px rgba(128, 128, 128, 0.7);
  transition: all 0.1s ease-in-out;
}
.cart__cards__card:hover {
  transform: translateX(5%);
}
.cart__cards__card:hover .remove {
  opacity: 1;
}
.cart__footer {
  height: 10%;
  width: 100%;
  background-color: #212120;
  border-radius: 0 0 0 35px;
}
.cart__footer h3 {
  color: #F9800D;
}

.selected {
  display: flex;
  justify-content: space-between;
  width: 90%;
  height: 100%;
  margin: auto;
}
.selected__content {
  width: 80%;
  height: 100%;
  display: flex;
}
.selected__content__image {
  width: 40%;
}
.selected__content__image img {
  width: 90%;
  height: 90%;
}
.selected__content__title {
  width: 65%;
  margin: 0;
  font-size: 1.2em;
}
.selected__count {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove {
  position: absolute;
  left: -35px;
  top: 47%;
  color: red;
  opacity: 0;
  transition: all 0.1s ease-in-out;
}

@media (max-width: 900px) {
  .cart {
    width: 50%;
  }
  .cart .cart__cards__card {
    height: 50px;
  }
  .cart .cart__cards__card img {
    height: 100%;
    max-width: 60px;
    max-height: 60px;
  }
  .cart .cart__total {
    min-height: 80px;
  }
  .cart .cart__footer {
    min-height: 50px;
  }
}
@media (max-width: 720px) {
  .cart {
    height: 86%;
    width: 95%;
    border-radius: 0px 0px 35px 35px;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    transition: all 0.3s ease-in-out;
  }
  .mobileCartOpen {
    margin-left: -100%;
  }
  .cartButton {
    width: 30%;
    height: 60%;
    cursor: pointer;
    color: white;
    background-color: #FFA752;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
  }
  .cartButton h2 {
    margin: 0;
  }
}